html {
  --nav-hover-color: #00acdc;
  --nav-color: #000;
  --header-bg-color: #fff;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

body {
  margin: 0;
  padding: 0;
}

.header {
  background-color: var(--header-bg-color);
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  margin: 0;
  padding: 0;
  display: inline-block;
}

.nav_list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav_link {
  color: var(--nav-color);
  text-transform: uppercase;
  height: 60px;
  padding: 0;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.nav_item:not(:first-child) {
  margin-left: 20px;
}

.logo__img {
  display: block;
}

.nav_item :hover {
  color: var(--nav-hover-color);
}

.is-active {
  color: var(--nav-hover-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--nav-hover-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

